import Loadings from '../loading/loading.vue'
import requireurl from '../../requist/requist.js'
export default {
	components:{
		Loadings
	},
	data(){
		const currentDate = this.getDate({
			format: true
		})
		return { 
			changeValue : '',
			rizhi:'',
			maxLong : -1,
			Loading : false,
			ProjectName:'',
			index: "0",
			startTime:currentDate,
			date: currentDate,
			tasktimer:'',
			array : [
				{
					status:1,
					name:'未开始'
				},
				{
					status:2,
					name:'进行中'
				},
				{
					status:3,
					name:'完成'
				},
			],
			name : '',
			changeID:0,
			TaskIds:0,
			TodayChangeTask : true
		}
	},
	onShow() {
		console.log('首页展示')
	},
	onLoad(option) {
		this.name = option.name		
		console.log(option.name	)
	},
	methods:{
		CommitTask(){
			this.Loading = true
			
			//修改任务信息
			if(this.name == 'change'){
				if(this.index == ''|| this.tasktimer == ''){
						this.Loading = false
						return;
				};
				if(this.changeID > 0){
					let obj = {
						status : (this.index * 1) + 1,
						hour : this.tasktimer,
						info : this.rizhi,
					}
					requireurl.getPutData(`p/v1/wx/journal/${this.changeID}/`,obj,this.tasklist,this.errtask)
				}else{
					let obj = {
						status : (this.index * 1) + 1,
						hour : this.tasktimer,
						info : this.rizhi,
						taskuser : this.TaskIds
					}
					requireurl.request('p/v1/wx/journal/',obj,this.tasklist,this.errtask)
				}
			}else{
				//新增任务信息
				if(this.index == '' || this.date == '' || this.changeValue =='' || this.tasktimer == '' || this.ProjectName == ''){
						this.Loading = false
						return;
				};
				
				let obj = {
					project:this.ProjectName,
					status : (this.index * 1) + 1,
					plan_start_time:this.startTime,
					plan_end_time : this.date,
					name : this.changeValue,
					hour : this.tasktimer,
					info : this.rizhi
				}
				requireurl.request('p/v1/wx/task/',obj,this.tasklist,this.errtask)
			}
			
		},
		tasklist(res){
			this.Loading = false
			console.log(res)
			if(res.statusCode == 200 || res.statusCode == 201){
				wx.showToast({
					title:'操作成功',
					icon: 'none'
				})
				uni.navigateBack({
				    delta: 1
				});
			}else{
				wx.showToast({
					title:res.data[0],
					icon: 'none'
				})
			}
		},
		//返回上一页
		goback(){
			uni.navigateBack({
			    delta: 1
			});
		},
	   change: function(e) {
			console.log(e)
			this.index = e.target.value 
		},
		bindDateChangeTwo:function(e){
			this.startTime = e.target.value
			console.log(e.target.value)
		},
		 bindDateChange: function(e) {
			this.date = e.target.value
			console.log(e.target.value)
		},
		 getDate(type) {
		            const date = new Date();
		            let year = date.getFullYear();
		            let month = date.getMonth() + 1;
		            let day = date.getDate();
		
		            if (type === 'start') {
		                year = year - 60;
		            } else if (type === 'end') {
		                year = year + 2;
		            }
		            month = month > 9 ? month : '0' + month;;
		            day = day > 9 ? day : '0' + day;
		            return `${year}-${month}-${day}`;
		        }
	},
	 computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	mounted() {
		var _that = this
		uni.getStorage({
		    key: 'taskid',
		    success: function (res) {
				console.log(res)
				if(res.data){
					_that.TaskIds = res.data.TaskIds;
					_that.index = (res.data.status - 1).toString();
					_that.changeValue = res.data.task_name;
					_that.ProjectName = res.data.project
				}
				uni.removeStorage({
				    key: 'taskid',
				    success: function (res) {
				        console.log('success');
				    }
				});
		    }
		});
		uni.getStorage({
		    key: 'info',
		    success: function (res) {
		        console.log(res.data);
				if(res.data){
					_that.ProjectName = res.data.project
					_that.changeID = res.data.enter.id;
					_that.index = (res.data.enter.status - 1).toString();
					_that.rizhi = res.data.enter.info;
					_that.tasktimer = res.data.enter.hour;
					_that.changeValue = res.data.task_name;
				}
				uni.removeStorage({
				    key: 'info',
				    success: function (res) {
				        console.log('success');
				    }
				});
		    }
		});
	},
	watch:{
		name(news,old){
			if(news == 'add'){
				
			}
		}
	}
}
